import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios';

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    Pesos : [],
    ItemPeso : {},    
    isEdit: false,
  },
  mutations: {
    getPesosMutation(state , pesos){
      state.Pesos = pesos;
    },
    getPesoByIdMutation(state , itemPeso){
      state.ItemPeso = itemPeso;
    },
    createPesoMutarion(state , itemPeso){
      state.Pesos = [ ...state.Pesos , itemPeso];
    },
    DeletePesoMutation(state , idPeso){
      state.Pesos.forEach(function(item, index, object) {
        if(item._id === idPeso){
          object.splice(index, 1);
        }
    });
    },
    UpdatePesoMutation(state , itemPeso){
      state.Pesos.forEach(function(item, index) {
        if(item._id === itemPeso._id){
          item.valorPeso = itemPeso.valorPeso
          item.fechaResgistro = itemPeso.fechaResgistro  
        }
    });
    },

    ReiniciarItemPeso(state){
     

    }

  },
  actions: {
    getPesos: async function ({ commit }) {
      await axios.get('https://apifastifycontrolpeso.herokuapp.com/peso')
      .then( res => {                        
        commit('getPesosMutation',res.data)
      } )
    },
    getPesoById: async function({commit} , idPeso){
      await axios.get(`https://apifastifycontrolpeso.herokuapp.com/peso/${idPeso}`)
      .then(  res => {
        commit('getPesoByIdMutation',res.data)
      } )
    },
    createPeso: async function({ commit }  , itemPeso){      
      await axios.post(`https://apifastifycontrolpeso.herokuapp.com/peso`,itemPeso)
      .then(res => {
        commit('createPesoMutarion', itemPeso )
      })
    },
    updatePeso : async function( {commit} ,itemPeso){
      
      const repItem = {
        _id:  itemPeso._id,
        valorPeso: parseFloat(itemPeso.valorPeso),
        fechaResgistro : itemPeso.fechaResgistro
      }
      
      console.log(repItem)
      await axios.put(`https://apifastifycontrolpeso.herokuapp.com/peso/${repItem._id}`, repItem)
      .then(  res => {
        commit('UpdatePesoMutation',repItem)
      })
    },
    deletePeso : async function({ commit},idPeso){
      await axios.delete(`https://apifastifycontrolpeso.herokuapp.com/peso/${idPeso}`)
      .then(  res => {
        commit('DeletePesoMutation',idPeso)
      })
    }
  },
  modules: {
  }
})
