<template>
<v-container>
  <v-row no-gutters>        
        <v-col cols="12" md="12" sm="12" >
            <v-container>
                 <v-data-table
               :headers="headers"
                :items="desserts"
                sort-by="calories"
                class="elevation-1">
            <template v-slot:top>
              <v-toolbar>      
                <v-dialog
                  v-model="dialog"
                  max-width="500px">
                  <template          
                  v-slot:activator="{ on, attrs }">
                    <v-btn
                      color="primary"
                      dark
                      class="mb-2"
                      v-bind="attrs"
                      v-on="on">
                      Nuevo
                    </v-btn>
                  </template>
                  <v-card>
                    <v-card-title>
                      {{ formTitle }}
                    </v-card-title>
                    <v-card-text>
                      <v-container>
                        <v-row>
                          <v-col
                            cols="12"
                            sm="6"
                            md="4"
                          >
                          
                            <v-menu
                      ref="menu"
                      v-model="menu"
                      :close-on-content-click="false"
                      :return-value.sync="date"
                      transition="scale-transition"
                    offset-y
                    min-width="auto"
                  >
                <template v-slot:activator="{ on, attrs }">
                  <v-text-field
                  
                    v-model="editedItem.fechaRegistro"
                    label="Click Aqui"
                    prepend-icon="mdi-calendar"
                    readonly
                    v-bind="attrs"
                    v-on="on"
                  ></v-text-field>
                </template>
                <v-date-picker
                  v-model="editedItem.fechaRegistro"
                  no-title
                  scrollable
                >
                  <v-spacer></v-spacer>
                  <v-btn
                    text
                    color="primary"
                    @click="menu = false"
                  >
                    Cancel
                  </v-btn>
                  <v-btn
                    text
                    color="primary"
                    @click="$refs.menu.save(editedItem.fechaRegistro)"
                  >
                    OK
                  </v-btn>
                </v-date-picker>
                      </v-menu>  
                          </v-col>
                          <v-col
                            cols="12"
                            sm="6"
                            md="4"
                          >
                            <v-text-field
                              v-model="editedItem.valorPeso"
                              label="Peso"
                            ></v-text-field>
                          </v-col>
                          <v-col
                            cols="12"
                            sm="6"
                            md="4"
                          > 
                          </v-col>                                                  
                        </v-row>
                      </v-container>
                    </v-card-text>

                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn
                        color="blue darken-1"
                        text
                        @click="close"
                      >
                        Cancelar
                      </v-btn>
                      <v-btn
                        color="blue darken-1"
                        text
                        @click="save"
                      >
                        Guardar
                      </v-btn>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
                <v-dialog v-model="dialogDelete" max-width="500px">
                  <v-card>
                    <v-card-title class="text-h5">¿Deseas eliminar este producto?</v-card-title>
                    <v-card-actions>
                      <v-spacer></v-spacer>
                      <v-btn color="blue darken-1" text @click="closeDelete">Cancelar</v-btn>
                      <v-btn color="blue darken-1" text @click="deleteItemConfirm">Aceptar</v-btn>
                      <v-spacer></v-spacer>
                    </v-card-actions>
                  </v-card>
                </v-dialog>
              </v-toolbar>
            </template>
            <template v-slot:item.actions="{ item }">
              <v-icon 
                small
                class="mr-2"
                @click="editItem(item)"
              >
                mdi-pencil
              </v-icon>
              <v-icon 
                small
                @click="deleteItem(item)"
              >
                mdi-delete
              </v-icon>       
            </template>
            <template v-slot:no-data>
              No existen Productos
            </template>
          </v-data-table>



                      </v-container>
                      </v-col>             
                </v-row>
      </v-container>  
</template>

<script>

import {  mapState , mapActions ,mapMutations}  from 'vuex'
import dateFormat  from "dateformat";

  export default {
    name: 'Home',
    dataProp : [],
    computed:{
      ...mapState(['Pesos','ItemPeso','isEdit']),
       formTitle () {
        return this.editedIndex === -1 ? 'Nuevo' : 'Editar'
      },
    },
    components: {      
    },
    data() {
      return {
         dialog: false,
      dialogDelete: false,
      headers: [
         { text: 'Peso', value: 'valorPeso' },
        { text: 'Fecha', value: 'fechaRegistro' },        
        { text: '', value: 'actions' },
      ],
      desserts: [],
      editedIndex: -1,
      editedItem: {         
        valorPeso: '',
        fechaRegistro: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
        
      },
      defaultItem: {
          valorPeso: '',
        fechaRegistro: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
        
      },
       
        

        date: (new Date(Date.now() - (new Date()).getTimezoneOffset() * 60000)).toISOString().substr(0, 10),
        menu: false,
        name : '',
        nameRules: [
        v => !!v || 'Name is required'        
      ],
      }
    },  
    created() {
      this.mostrarPesos();        
    },  
    methods:{      
       ...mapMutations(['ChangePeso','ReiniciarItemPeso','ChangeEdit']),
       ...mapActions(['getPesos','getPesoById','createPeso','deletePeso','updatePeso']),
      async mostrarPesos(){
          await this.getPesos();          
            this.Pesos.forEach(peso => {                 
                 peso.fechaRegistro =dateFormat(Date.parse(peso.fechaRegistro),"dd/mm/yyyy");            
                 this.desserts.push(peso)
            });        

       },          
      async deleteItem (item) {
          
           
      } ,       
        editItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialog = true
     
      },

       deleteItem (item) {
        this.editedIndex = this.desserts.indexOf(item)
        this.editedItem = Object.assign({}, item)
        this.dialogDelete = true;
       
       
      },
       async deleteItemConfirm () {                        
           await this.deletePeso(this.editedItem._id) 
          this.desserts = this.Pesos   
        this.closeDelete()
      },

      close () {
        this.dialog = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      closeDelete () {
        this.dialogDelete = false
        this.$nextTick(() => {
          this.editedItem = Object.assign({}, this.defaultItem)
          this.editedIndex = -1
        })
      },
      async save () {
        if (this.editedIndex > -1) {
          Object.assign(this.desserts[this.editedIndex], this.editedItem)                      
           await this.updatePeso(this.editedItem)
            this.desserts = this.Pesos       

        } else {
            
            await this.createPeso(this.editedItem)
            this.desserts = this.Pesos       
        }
        this.close()
      },
    }
  }
</script>
